<script>
import InputBox from './InputBox.vue';
import colors from '../scss/_theme.scss';
import Button from './Button.vue'
import TransitionExpand from './TransitionExpand.vue'
import Dropdown from './Dropdown.vue'
import SvgIcon from './svg-icon';
import Popup from './Popup.vue';

export default {
    name: 'WalletBalanceTile',
    props: {
        title: {
            type: String,
            required: true
        },
        tooltipText: {
            type: String,
            default: ""
        },
        balance: {
            type: Number,
            default: 0
        },
        defaultTargetAddress: {
            type: String,
            required: true
        },
        dropdownOptions: {
            type: Array,
            required: true,
            default: () => ['Other public address']
        },
        handleWalletTransfer: {
          type: Function,
          required: true,
          default: () => {}
        },
        minTransferAmount: {
            type: Number,
            default: 1000
        }
    },
    components: {
        Button,
        TransitionExpand,
        Dropdown,
        'bt-input-box': InputBox,
        'svg-icon': SvgIcon,
        Popup

    },
    data() {
        return {
            isExpanded: false,
            colors,
            amount: '',
            publicAddress: '',
            dropdownSelection: null,
            showPopup: false,
            popupState: 0, //['confirmation', 'processing', 'success']
            transferRes: '',
        }
    },
    methods: {
        handleExpand() {
            this.isExpanded = !this.isExpanded
            if(this.isExpanded === false){
                this.dropdownSelection = null
            }
        },
        handleSend() {
            this.showPopup = true
        },
        handlePopoutConfirm() {
            this.popupState = 1
            this.handleWalletTransfer({amount: this.amount})
            .then((res) => {
                setTimeout(() => {
                    this.transferRes = res
                    this.popupState = 2
                    this.amount = ''
                }, 500)
            })
        },
        handlePopoutClose() {
            this.showPopup = false
            this.popupState = 0
        },
    },
    computed: {
        isAmountValid() {
            return this.amount >= this.minTransferAmount && this.amount <= this.balance
        },
        isAmountEmpty() {
            return this.amount == null || this.amount === ""
        },
        amountMessage() {
            let message = `The minimum transfer is ${this.minTransferAmount} BTT`
            if (this.amount < this.minTransferAmount && !this.isAmountEmpty) {
                message = `Please enter at least ${this.minTransferAmount} BTT`
            }
            if (this.amount > this.balance && !this.isAmountEmpty) {
                message = "You don't have enough balance to create this transfer."
            }

            return message

        },
        amountMessageColor() {
            return this.isAmountValid ? colors.btfsBlue500 : colors.gray700
        },
        isPublicAddressValid() {
            return this.publicAddress === "test" || this.publicAddress === this.defaultTargetAddress
        },
        isPublicAddressEmpty() {
            return this.publicAddress == null || this.publicAddress === ""
        },
        publicAddressMessage() {
            return (!this.isPublicAddressValid && !this.isPublicAddressEmpty) ? "The public address doesn't exist" : ""
        }
    },
    watch: {
        dropdownSelection() {
            if(this.dropdownSelection === "Transfer to" || this.dropdownSelection === null){
                this.publicAddress = ""
                return
            }
            
            this.publicAddress = (this.dropdownSelection === "Other public address") ? "" : this.defaultTargetAddress
        }
    }
};
</script>

<template>
    <div class="wallet-balance-tile">
        <div class="upper">
            <p class="title">
                {{title}} 
                <svg-icon 
                :hasFill="true" 
                icon="help" 
                class="help-icon" 
                v-tooltip.bottom="{ content: tooltipText, classes: ['tooltip'], hideOnTargetClick: false, autoHide: false}"/>
            </p>
            <p class="balance">{{balance.toLocaleString('en-US', {maximumFractionDigits: 6}) }} BTT</p>
            <div class="transfer-button-container">
                <Button type="text" :cb="handleExpand" :isOpen="isExpanded">Transfer</Button>
            </div>
        </div>
        <TransitionExpand>
            <div v-if="isExpanded" class="lower">
                <div class="lower-inner-container">
                    <bt-input-box 
                        v-model="amount"
                        inputType="number"
                        placeholder="Amount"
                        :showMessage="true"
                        :messageColor="amountMessageColor"
                        :borderColor="colors.btfsBlue500"
                        :placeholderColor="colors.gray700"
                        :message="amountMessage"
                        :delayEmit="true"
                        :isInvalid="!isAmountValid && !isAmountEmpty"
                        v-if="dropdownSelection !== 'Other public address'"
                    />
    
                    <Dropdown placeholder="Transfer to" :options="dropdownOptions" @selection="(val) => dropdownSelection = val"/>
                    
                    <div 
                        class="coming-soon"
                        v-if="dropdownSelection === 'Other public address'"
                        >
                            <!-- temporary under development text slot -->
                            <slot/>
                    </div>
                    <!-- <bt-input-box 
                        v-model="publicAddress"
                        inputType="text"
                        placeholder="Public address"
                        :showMessage="true"
                        :borderColor="colors.btfsBlue500"
                        :placeholderColor="colors.gray700"
                        :message="publicAddressMessage"
                        v-if="dropdownSelection === 'Other public address'"
                        :delayEmit="true"
                        :isInvalid="!isPublicAddressValid && !isPublicAddressEmpty"
                    /> -->
                    <div class="button-container">
                        <Button :disabled="!this.isPublicAddressValid || this.isPublicAddressEmpty || !this.isAmountValid" :cb="handleSend">Send</Button>
                    </div>
                </div>
            </div>
        </TransitionExpand>
        <Popup :isOpen="!showPopup" :closable="popupState === 0" @onClose="handlePopoutClose">
            <div v-if="popupState === 0">
                <p class="popup-header">Transfer confirmation</p>
                <p class="popup-description">You are about to send {{amount}} BTT.</p>
                <div class="popup-confirmation-buttons">
                    <Button :cb="handlePopoutClose" type="text">Cancel</Button>
                    <Button :cb="handlePopoutConfirm ">Continue</Button>
                </div>
            </div>
            <div v-if="popupState === 1">
                <p class="popup-header">Transfer processing</p>
                <p class="popup-description">The transfer may take ~1 min before the balance change is shown.</p>

                <img src="../assets/svg/spinner.svg" class="spinner-icon"/>
            </div> 
            <div v-if="popupState === 2">
                <p class="popup-header">Transfer {{transferRes}}</p>
                <p class="popup-description hide"></p>

                <div class="">
                    <Button :cb="handlePopoutClose">Done</Button>
                </div>
            </div> 
        </Popup>
    </div>
</template>

