        display: flex;
        flex-direction: column;
        position: relative;
        padding: 24px;
        border-bottom: 1px solid $gray-300;

        p {
            display: flex;
            align-items: center;
        }

        .title {
            font-family: Lato;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 24px;
            letter-spacing: 2px;
            text-transform: uppercase;

            .help-icon{ 
                fill: $gray-500;
                margin: 0 0 0 8px;
            }
        }

        .balance {
            font-family: Lato;
            font-style: normal;
            font-weight: bold;
            font-size: 34px;
            line-height: 40px;
            letter-spacing: 0.25px;
            color: $btfs-blue-500-main;
            margin: 24px 0 0 0;
        }

        .transfer-button-container {
            position: absolute;
            bottom: 16px;
            right: 24px;

        }
    }

    .lower {
        display: flex;
        flex-direction: column;
        position: relative;

        .lower-inner-container {
            padding: 24px;

                .button-container {
                    width:100%;
                    display: flex;
                    justify-content: flex-end;
                }

                .coming-soon {
                    margin-bottom: 24px;
                }
        }
    }
}

</style>