<script>
import Button from '../components/Button.vue'
import SetStorageSize from '../components/SetStorageSize.vue'

export default {
	name: 'EditStorageSize',
	components: {
		Button,
		SetStorageSize
	},
	props: {
		storageSpace: {
			type:Object,
			default:function() {
				return {
					minimum: {
						type: Number,
						required: false,
						default:0
					},
					used: {
						type:Number,
						required:true,
						default:0
					},
					cap: {
						type:Number,
						required:true,
						default:0
					},
					total: {
						type:Number,
						required:true,
						default:1000
					}
				}
			}
		},
		handleClose: {
			type: Function,
			default: () => {}
		}
	},
	data() {
		return {
			selectedStorageValue: this.storageSpace.cap,
		}
	},
	methods: {
		handleCommitClick() {
			this.handleClose()
			this.$store.dispatch('storageAnnounce', {
				hostStorageMax: this.selectedStorageValue,
				local: false
			})
		}
	},
	computed: {
		canCommit() {
			let inputTooLow = this.selectedStorageValue <=this.storageSpace.used || this.selectedStorageValue < this.storageSpace.minimum
			let inputTooHigh = this.selectedStorageValue > this.storageSpace.total
			if(inputTooLow || inputTooHigh) {
				return false
			}
			return true
		}
	}

};

</script>

<template>
<div class="edit-storage-size-bg">
  <div class="edit-storage-size-container">
    <img src="../assets/svg/close.svg" class="close-button" @click="handleClose"/>
    <p class="header">Edit host storage size</p>
    <p class="description">
      {{storageSpace.used}} GB is your current storage used by BTFS. Try changing your storage commitment size in the input field below to see how much more you can earn.
    </p>
    <SetStorageSize 
      :storageSpace="storageSpace" 
      :isDisabled="false"
      :useSlider="false"
      @inputChange="selectedStorageValue = $event"/>
    <div class="button-container">
        <Button :isDisabled="!canCommit" :cb="handleCommitClick">Commit Storage</Button>
    </div>
  </div>
</div>
</template>

