<script>
import SlideBar from './SlideBar'
import colors from '../scss/_theme.scss'
import SvgIcon  from  '../components/svg-icon.vue';

export default {
	name: 'ViewStorageSize',
	components: {
		SlideBar,
		'svg-icon': SvgIcon,
	},
	props: {
		storageSpace: {
			type:Object,
			default:function() {
				return {
					minimum: {
						type: Number,
						required: false,
						default:0
					},
					used: {
						type:Number,
						required:true,
						default:0
					},
					cap: {
						type:Number,
						required:true,
						default:0
					},
					total: {
						type:Number,
						required:true,
						default:1000
					}
				}
			}
		},
		handleEdit: {
			type: Function,
			default: () => {}
		}
	},
	data() {
		let data = this.initData()
		return {
			current: data,
			warningThreshold:10
		}
		
	},
	methods: {
		initData(){
			let initialSpaceUsedPercent = ((this.storageSpace.used) / (this.storageSpace.cap)).toFixed(3)
			initialSpaceUsedPercent = parseFloat(initialSpaceUsedPercent)
			let space = getStorageUsedAndRemaining(0, this.storageSpace.cap, initialSpaceUsedPercent)
			if(initialSpaceUsedPercent < 0){
				initialSpaceUsedPercent = 0
			}
			else if(initialSpaceUsedPercent > 1){
				initialSpaceUsedPercent = 1
			}

			return {
				used:space.used,
				available:space.available,
				initialPercent:initialSpaceUsedPercent
			}
		}
	},
	computed:{
		earnings() {
			return gbToBTT(this.current.used)
		},
		remainingSpace() {
			return this.storageSpace.cap - this.storageSpace.used
		},
		storageState() {
			let obj = {
				bar:{
					bgColor:undefined,
					fillColor:undefined
				},
				usedTextStyle: {
					color:colors.btfsBlue500,
				},
				remainingTextStyle: {
					color:colors.gray500,
				},
				state: ""
			}
			if(this.remainingSpace <= this.warningThreshold){
				obj.bar.bgColor = '#FAEABF'
				obj.bar.fillColor = undefined
				obj.remainingTextStyle = {
					color:'#F7B500'
				}
				obj.state = "warning"
			}
			if(this.remainingSpace <= 0){
				obj.bar.bgColor='#EB5757'
				obj.bar.fillColor='#EB5757'
				obj.usedTextStyle = {
					color:'#EB5757'
				}
				obj.remainingTextStyle = {
					color:'#EB5757'
				}
				obj.state = "danger"
			}
			return obj
		},
	},
	watch:{
		storageSpace : function(){
			let newData = this.initData()
			this.current = {
				...this.current,
				...newData
			}
		}
	}
};

function getStorageUsedAndRemaining(min, max, percent){
	let spaceUsed = (max - min) * percent + min;
	let spaceAvailable = max - spaceUsed
	return {
		used:parseInt(spaceUsed.toFixed(0)),
		available:parseInt(spaceAvailable.toFixed(0))
	}
}

function gbToBTT(gb){
	let btt = gb * 8
	btt = parseInt(btt)
	return btt.toLocaleString()
}
</script>

<template>
	<div class="set-storage-size-container">
		<div class="storage">
			<img src="../assets/svg/edit.svg" class="edit-button" @click="handleEdit"/>
			<p class="title">Host Storage Size</p>
			<div class="storage-container">
				<div class="storage-space">
					<div class="storage-space-used" :style="this.storageState.usedTextStyle">
						{{this.current.used}} GB used
					</div>
					<div class="storage-space-available" :style="this.storageState.remainingTextStyle">
						{{this.current.available}} GB remaining
					</div>
				</div>
				<SlideBar
					:initialPercent="this.current.initialPercent"
					:slideable="false"
					:height="24"
					:bgColor="this.storageState.bar.bgColor"
					:fillColor="this.storageState.bar.fillColor"/>
				<div class="error-container">
					<svg-icon v-if="this.storageState.state === 'warning'" icon="info" class="info-icon warning" :hasFill="true" />
					<p v-if="this.storageState.state === 'warning'" class="warning">
						Your storage is almost full, please increase your storage commitment size. 
					</p>

					<svg-icon v-if="this.storageState.state === 'danger'" icon="info" class="info-icon danger" :hasFill="true" />
					<p v-if="this.storageState.state === 'danger'" class="danger">
						Your storage is full, please increase your storage commitment size.  
					</p>
				</div>
			</div>
		</div>
	</div>
</template>

