<script>
import Nav from './components/Nav.vue'

export default {
  name: 'App',
  components: {
    Nav,
  },
  data(){
    return {
      isLoading: true,
      isSynced: false
    }
  },
  methods:{
    initSetup() {
      let hostConfigAPI = this.$store.dispatch('getHostConfig');
      let walletConfigAPI = this.$store.dispatch('getWalletConfig');
      let storageStatsInfoAPI = this.$store.dispatch('storageStatsInfo', !this.isSynced)
      let metaStoresAPI = this.initMetaStores()
      let walletStoresAPI = this.initWalletStores()

      return Promise.all([
        hostConfigAPI,
        walletConfigAPI,
        storageStatsInfoAPI,
        metaStoresAPI,
        walletStoresAPI
      ])
    },
    initSync() {
      let storageStatsSync = this.$store.dispatch('storageStatsSync')
      let storageContractsSync = this.$store.dispatch('storageContractsSync')
      return Promise.all([
        storageStatsSync,
        storageContractsSync
      ]).then((res) => {
        // console.log('sync res: ',res)
        this.isSynced = res.reduce((acc, curr) => acc && curr)
      })
    },
    initStorageStores() {
      let storageInfoAPI = this.$store.dispatch('storageInfo')
      let storageStatsInfoAPI = this.$store.dispatch('storageStatsInfo', !this.isSynced)
      let storageContractsAPI = this.$store.dispatch('storageContractsInfo')
      let storageContractsListAPI = this.$store.dispatch('storageContractsList')

      return Promise.all([
        storageInfoAPI,
        storageStatsInfoAPI,
        storageContractsAPI,
        storageContractsListAPI,
      ])
    },
    initMetaStores() {
      let metaRepoStatAPI = this.$store.dispatch('metaRepoStat');
      let metaIdAPI = this.$store.dispatch('metaId');
      let metaVersionAPI = this.$store.dispatch('metaVersion');

      return Promise.all([
        metaRepoStatAPI,
        metaIdAPI,
        metaVersionAPI,
      ])
    },
    initWalletStores() {
      let walletBalanceAPI = this.$store.dispatch('walletBalance');
      let walletTransactionsAPI = this.$store.dispatch('walletTransactions')

      return Promise.all([
        walletBalanceAPI,
        walletTransactionsAPI
      ])
    },
    createStorageSpaceObj() {
      let storageSpace = {
        used:0,
        cap:32,
        total:1000,
        minimum:32
      }
      if(!this.isLoading){
        storageSpace.used = this.storageSpaceUsed
        storageSpace.cap = this.storageSpaceCap
        storageSpace.total = this.storageSpaceTotal
      }
      return storageSpace
    },
    isValidRoute(routeName){
      let isValidRoute = this.$router.options.routes.reduce((acc, val) => {
        return (val.name === routeName)||acc
      }, false)
      return isValidRoute
    },
    isSetupRoute(routeName) {
      return routeName.includes('Add') || routeName.includes('Setup')
    },
    routeType(routeName) {
      if(routeName.startsWith('Host')) return 'host'
      if(routeName.startsWith('Wallet')) return 'wallet'
      if(routeName.startsWith('Settings')) return 'settings'
      return null
    },
  },
  created(){

      this.initSetup()
      .then(() => {
        // ROUTE GUARD
        if(!this.isValidRoute(this.route)){
          this.$router.push('/')
        }
        let isSetupRoute = this.isSetupRoute(this.route)
        let routeType = this.routeType(this.route)

        switch(routeType){
          case 'host':
            if(!this.isHostSetup && (!isSetupRoute)){

              this.$router.push('/host/add')
            }
            else if(this.isHostSetup && (isSetupRoute)){

              this.$router.push('/')
            }
            break

          case 'wallet':
            if(!this.isWalletSetup && (!isSetupRoute)){

              this.$router.push('/wallet/add')
            }
            else if(this.isWalletSetup && (isSetupRoute)){

              this.$router.push('/wallet')
            }
            break
        }
        
        // ROUTE GUARD IN APP
        this.$router.beforeEach((to, from, next) => {

          if(!this.isValidRoute(to.name)){
            return
          }
          let isSetupRoute = this.isSetupRoute(to.name)
          let routeType = this.routeType(to.name)

          switch(routeType){
            case 'host':
              if(!this.isHostSetup && (!isSetupRoute) ){
                next('/host/add')
              } 
              else if(this.isHostSetup && (isSetupRoute) ){
                next({name: 'HostDashboard'})
              }
              else {
                next()
              }
              break

            case 'wallet':
              if(!this.isWalletSetup && (!isSetupRoute) ){
                next('/wallet/add')
              } 
              else if(this.isWalletSetup && (isSetupRoute) ){
                next({name: 'WalletDashboard'})
              }
              else {
                next()
              }
              break

            default:
              next()
          }
        })
        // ROUTE GUARD END

        if(!this.isHostSetup){
          this.initSync()
          this.isLoading = false;
          return Promise.reject('host not setup')
        }
        return this.initSync()
      })
      .then(() => {
        return this.initStorageStores()
      })
      .then(() => {
        this.isLoading = false;
      })
      .catch(() => {
        // console.log('[initialization]',e)
      })
  },
  computed:{
    route(){
      return this.$route.name
    },
    isHostSetup(){
      return this.$store.state.config.host.initialized
    },
    isWalletSetup(){
      return this.$store.state.config.wallet.initialized
    },
    storageSpaceUsed(){
      let storageState = this.$store.state.storage.value
      return bToGB(storageState.host_stats.storage_used)
    },
    storageSpaceCap(){
      let storageState = this.$store.state.storage.value
      return bToGB(storageState.host_stats.storage_cap)
    },
    storageSpaceTotal(){
      let storageState = this.$store.state.storage.value
      let storageDiskAvailable = storageState.host_stats.storage_disk_available
      let storageUsed = storageState.host_stats.storage_used
      let storageAvailableCap = storageDiskAvailable + storageUsed
      return bToGB(storageAvailableCap)
    },
    hostOnline() {
      return this.$store.state.storage.value.host_stats.online
    },
    hostScore() {
      return this.$store.state.storage.value.host_stats.score
    },
    hostUptime() {
      return this.$store.state.storage.value.host_stats.uptime
    },
    hostContractsNum() {
      return this.$store.state.storage.contracts.active_contract_num
    },
    earnings() {
      return this.$store.state.storage.contracts.compensation_paid
    },
    potentialEarnings() {
      return this.$store.state.storage.contracts.compensation_outstanding
    },
    hostPricing() {
      return 7680
    },
    nextPayment() {
      if(this.$store.state.storage.contractsList.contracts.length > 0) {
        let date = this.$store.state.storage.contractsList.contracts[0]
        date = date.next_escrow_time
        date = new Date(date)
        date = date.toDateString()
        date = date.slice(3)
        date = date.slice(0,7) + ',' + date.slice(7)
        return date
      }
      return null
    },
    nodeVersion() {
      return this.$store.state.meta.version.Version
    },
    walletInAppBalance() {
      return this.$store.state.wallet.balance.BtfsWalletBalance
    },
    walletCryptoBalance() {
      return this.$store.state.wallet.balance.BttWalletBalance
    },
    walletRecoveryPhrase() {
      return this.$store.state.wallet.keys.Mnemonic
    },
    walletTransactions() {
      return this.$store.state.wallet.transactions
    },
    nodeID() {
      return this.$store.state.meta.id.ID
    },
    routeProps(){
      if(this.route === "HostAdd") {
        let hasEnoughSpace = this.storageSpaceTotal > 32
        return {
          hasEnoughSpace,
        }
      }
      else if(this.route === "HostDashboard") {
        let storageSpace = this.createStorageSpaceObj()
        return{
          online: this.hostOnline,
          score: this.hostScore,
          uptime: this.hostUptime,
          contractsNum: this.hostContractsNum,
          earnings: this.earnings,
          potentialEarnings: this.potentialEarnings,
          hostPricing: this.hostPricing,
          nextPayment: this.nextPayment,
          storageSpace: storageSpace,
          isSynced: this.isSynced
        }
      }
      else if(this.route === "HostSetup") {
        let storageSpace = this.createStorageSpaceObj()
        return {
          storageSpace,
          storageAnnounce: (hostStorageMax) => {
            this.$store.dispatch('storageAnnounce', {
              hostStorageMax: hostStorageMax,
              local: !this.isSynced
            }) 
          },
          setInitializationFlag: () => {

            this.initSync()
            .then(() => {
              return this.initStorageStores()
            })

            return this.$store.dispatch('setHostConfig')
          }
        }
      }
      else if(this.route === "Settings") {
        return {
          version: this.nodeVersion,
          id: this.nodeID
        }
      }
      else if(this.route === "WalletDashboard") {
        return {
          id: this.nodeID,
          transactions: this.walletTransactions,
          handleDeposit: (options) => {
            return this.$store.dispatch('walletDeposit', options)
          },
          handleWithdraw: (options) => {
            return this.$store.dispatch('walletWithdraw', options)
          },
          inAppBalance: this.walletInAppBalance,
          cryptoBalance: this.walletCryptoBalance,
          handleRefresh: () => {
            this.$store.dispatch('walletBalance')
            this.$store.dispatch('walletTransactions')
          }
        }
      }
      else if(this.route === "WalletSetup") {
        return {
          setInitializationFlag: () => {
            return this.$store.dispatch('setWalletConfig')
          },
          setPassword: (options) => {
            return this.$store.dispatch('walletPassword', options)
          },
          getKeys: (options) => {
            return this.$store.dispatch('walletKeys', options)
          },
          recoveryPhrases: this.walletRecoveryPhrase
        }
      }
      return {}
    }
  }

}

function bToGB(b) {
  let gb = b/Math.pow(1000, 3)
  gb = Math.floor(gb)
  return gb
}

</script>

<template>
  <div id="app">
    <div class="main">
      <Nav :nodeID="nodeID"/>
      <router-view v-bind="routeProps" v-if="!isLoading" class="view"/>
    </div>
  </div>
</template>

