import Vue from 'vue';
import routes from './routes';
const { request } = routes;

const state = {
    balance: {
        BtfsWalletBalance: 0,
        BttWalletBalance: 0
    },
    transactions: [
    {
        id: 6,
        created: '1579597829',
        amount: 33332200,
        type: 'Deposit',
        status: 'complete',
        message: ''
    }
  ],
  keys: {
    PrivateKey: '',
    Mnemonic:[]
  }
};
const mutations = {
    setBalance(s, balance) {
        let formattedBalance = {
            BtfsWalletBalance: balance.BtfsWalletBalance / 1000000,
            BttWalletBalance: balance.BttWalletBalance / 1000000
        }
        Vue.set(s, 'balance', formattedBalance)
    },
    setKeys(s, options) {
        let mnemonic = options.res.Mnemonic
        //handle recovery phrase encryption here
        // console.log(options.res)
        // let encryptedRes = options.res.PrivateKey
        // console.log(encryptedRes, options.password)
        // let decrypted = utils.decrypt_with_aes(options.password, encryptedRes)
        // console.log(decrypted)

        let formattedMnemonic = (mnemonic === "") ? [] : mnemonic.split(' ')
        let formattedKeys = {
            PrivateKey: options.res.PrivateKey,
            Mnemonic: formattedMnemonic
        }
        Vue.set(s, 'keys', formattedKeys);
    },
    setTransactions(s, transactions) {
        let formattedTransactions = transactions.map((x, index) => {
        let d = new Date(x.time_create)

            return {
                id: index,
                created: d,
                amount: x.amount/1000000,
                type: 'Deposit',
                status: x.status,
                from: x.from,
                to: x.to

            }
        })
        formattedTransactions.sort(function(a,b){
          // Turn your strings into dates, and then subtract them
          // to get a value that is either negative, positive, or zero.
          return new Date(b.created) - new Date(a.created);
        });

        Vue.set(s, 'transactions', formattedTransactions);
    },
};
const getters = {
    balance(s) {
        return s.balance
    },
    recoveryPhrase(s) {
        return s.recoveryPhrase;
    },
    transactions(s) {
        return s.transactions
    }
};
const actions = {
    walletBalance(context) {
        return request({
            endpoint: 'v1/wallet/balance',
            method: 'POST',
            
        }).then((res) => {
            let data = JSON.parse(res)
            context.commit('setBalance', data);
        });
    },
    walletPassword(context, options) {
        return request({
            endpoint: 'v1/wallet/password',
            method: 'POST',
            params: {
                arg: options.password,
            }
            
        }).then(() => {
        });
    },
    walletKeys(context, options) {
        return request({
            endpoint: 'v1/wallet/keys',
            method: 'POST',
        }).then((res) => {
            context.commit('setKeys', {
                res: JSON.parse(res),
                password: options.password
            });
        });
    },
    walletTransactions(context) {
        return request({
            endpoint: 'v1/wallet/transactions',
            method: 'POST',
            
        }).then((res) => {
            let data = JSON.parse(res)
            context.commit('setTransactions', data);
        });
    },
    walletDeposit(context, options) {
        return request({
            endpoint: 'v1/wallet/deposit',
            method: 'POST',
            params: {
                arg: options.amount*1000000,
                a:''
            }
        }).then((res) => {
            context.dispatch('walletTransactions')
            context.dispatch('walletBalance')
            let data = JSON.parse(res)
            if ("Type" in data) {
                if (data.Type === "error") {
                    return 'failed'
                }
            }
            return 'success'
        });
    },
    walletWithdraw(context, options) {
        return request({
            endpoint: 'v1/wallet/withdraw',
            method: 'POST',
            params: {
                arg: options.amount*1000000
            }
        }).then((res) => {
            context.dispatch('walletTransactions')
            context.dispatch('walletBalance')
            let data = JSON.parse(res)
            if ("Type" in data) {
                if (data.Type === "error") {
                    return 'failed'
                }
            }
            return 'success'
        })
    }
};

export default {
    state,
    getters,
    mutations,
    actions,
};