<script>
import DashboardStat from '../components/DashboardStat.vue'
import ViewStorageSize from '../components/ViewStorageSize.vue'
import EditStorageSize from '../components/EditStorageSize.vue'

export default {
  name: 'HostDashboard',
  props: {
    online: {
      type:Boolean,
      required:false,
      default:false
    },
    score: {
      type:Number,
      required:false,
      default:0
    },
    uptime: {
      type:Number,
      required:false,
      default:0
    },
    contractsNum: {
      type:Number,
      required:false,
      default:0
    },
    earnings: {
      type:Number,
      required:false,
      default:0
    },
    potentialEarnings: {
      type:Number,
      required:false,
      default:0
    },
    hostPricing: {
      type:Number,
      required:false,
      default:0
    },
    nextPayment: {
      type:String,
      required:false,
      default:""
    },
    storageSpace: {
      type:Object,
      default:function() {
        return {
          minimum: {
            type: Number,
            required: false,
            default:0
          },
          used: {
            type:Number,
            required:true,
            default:0
          },
          total: {
            type:Number,
            required:true,
            default:1000
          }
        }
      }
    },
    isSynced: {
      type:Boolean,
      required:true,
      default:false
    },
  },
  components: {
    DashboardStat,
    ViewStorageSize,
    EditStorageSize,
  },
  methods: {
    onRefresh() {
      this.refresh()
    },
    toggleEditStorageSize() {
      this.showEditStorageSize = !this.showEditStorageSize
    }
  },
  data() {
    return {
      showEditStorageSize: false
    }
  },
  computed: {
    onlineStatusText() {
      return (this.online) ? "Online" : "Offline"
    },
    onlineStatusTooltip() {
      if(this.online){
        return "You are connected to BTFS network and hosting."
      }
      return "You are not connected to BTFS network, this could be due to many reasons, please check FAQs: <a href='https://docs.btfs.io/docs/faq' target='_blank' rel='noreferrer noopener'>https://docs.btfs.io/docs/faq</a>"
    }
  }
}
</script>

<template>
  <div class="dashboard">
      <div class="dashboard-header">
        <DashboardStat 
          title="Status"
          :tooltipText="onlineStatusTooltip">
          <span class="online-status" :class="{offline: !online, hidden: !isSynced}">{{onlineStatusText}}</span>
        </DashboardStat>
        <DashboardStat 
          title="Score"
          tooltipText="This is an indicator of how reliable a host is, based on uptime, age, etc.">
          <span :class="{hidden: !isSynced}">{{score}}/10</span>
        </DashboardStat>
        <DashboardStat 
          title="Uptime"
          tooltipText="Percentage of time when a host is online and can prove the file replication.">
          <span :class="{hidden: !isSynced}">{{(uptime*100).toFixed(0)}}%</span>
        </DashboardStat>
        <DashboardStat 
          title="Contracts"
          tooltipText="Each contract represents a host’s commitment to a certain file shard’s storage length, size, and price.">
          <span :class="{hidden: !isSynced}">{{contractsNum}}</span>
        </DashboardStat>
      </div>
      <div class="dashboard-container"> 
        <div class="earnings-container">
          <div class="earnings">
            <p class="title">
              Earnings to-date
            </p>
            <p class="amount">
              <span :class="{hidden: !isSynced}">{{earnings.toLocaleString()}} BTT </span>
            </p>
          </div>
          <div class="future-earnings">
            <div class="future-potential-earnings">
              <p class="secondary-title">Future potential earnings</p>
              <p class="secondary-content">
                {{potentialEarnings.toLocaleString()}} BTT
              </p>
            </div>
            <div class="next-payment-date">
              <p class="secondary-title">Next payment date</p>
              <p class="secondary-content">
                {{nextPayment}}
              </p>
            </div>
          </div>
          <div class="pricing">
            <p class="secondary-title">Current host pricing</p>
            <p class="secondary-content">
              {{hostPricing.toLocaleString()}} BTT (TB/Month)
            </p>
          </div>
        </div>

        <ViewStorageSize
        :storageSpace="storageSpace" 
        :isDisabled="false"
        :handleEdit="toggleEditStorageSize"/>
      </div>
      <div class="syncing-bg" v-if="!isSynced">
        <div class="syncing-container">
          <p class="syncing-header">Syncing...</p>
          <p class="syncing-description">Currently syncing with the BTFS network, please refresh this page after 30-45 minutes.</p>
          <img src="../assets/svg/spinner.svg" class="spinner-icon"/>
        </div>
      </div>
    <EditStorageSize
      v-if = "showEditStorageSize"
      :storageSpace = "storageSpace"
      :handleClose = "toggleEditStorageSize">
    </EditStorageSize>
  </div>
</template>

