<script>
import colors from '../scss/_theme.scss';
import StepIndicator from '../components/StepIndicator.vue'
import Button from '../components/Button.vue'
import PasswordCreate from '../components/PasswordCreate.vue'
import RecoveryPhrase from '../components/RecoveryPhrase.vue'

export default {
	name: 'WalletSetup',
	props: {
		recoveryPhrases: {
			type: Array,
			default() {
				return ['Animated', 'Useful', 'Aunt', 'Grab', 'Visit', 'Sales', 'Brisk', 'Glory', 'Blind', 'Exotic', 'upset', 'valve'];
			}
		},
		setInitializationFlag: {
			type: Function,
			default: () => {}
		},
		setPassword: {
			type: Function,
			default: () => {}
		},
		getKeys: {
			type: Function,
			default: () => {}
		}
	},
	components: {
		StepIndicator,
		Button,
		PasswordCreate,
		RecoveryPhrase
	},
	data() {
		
		return {
			steps: ['1', '2', '3'],
			current: 1,
			colors,
			passwordsMatch: false,
			recoveryPhraseMatches: null,
			password: ''
		}
	},
	computed: {
		
	},
	methods: {
		handleRecoveryPhraseMatches(val) {
			this.recoveryPhraseMatches = val
		},
		nextStep() {
			this.current += 1
			if(this.current === 3){
				this.recoveryPhraseMatches = null;
			}
		},
		prevStep() {
			this.current -= 1
		},
		handleSetupComplete() {
			this.setInitializationFlag()
			.then(() => {
				this.$router.push('/wallet')
			})
		},
		handlePasswordSet() {
			this.getKeys({password: this.password})
			this.setPassword({password:this.password})
			.then(() => {
				return this.getKeys({password: this.password})
			})
			.then (() => {
				if(this.recoveryPhrases === undefined || this.recoveryPhrases.length == 0) {
					this.handleSetupComplete()
				}
				else{
					this.nextStep()
				}

			})
		}
	}
}
</script>

<template>
	<div class="wallet-setup">
			<StepIndicator class="step-container" :steps="steps" :current="current"></StepIndicator>

			<div class="wallet-setup-1" v-if="current === 1">
				<div class="header-container">
					<p class="header">Wallet password</p>
					<p class="description">
						Create a strong password and save it in a safe place. We do not have access to your password and will not be able to restore it. Losing your password may result in losing access to your funds. 
					</p>
				</div>

				<div class="password-container">
					<PasswordCreate @passwordsMatch="(val)=>{passwordsMatch = val}" @password="(val) => {password = val}"/>
				</div>

				<div class="text-container">
					<p class="text-sm">
						By clicking “Continue” you agree to the <a href="https://www.bittorrent.com/legal/terms-of-use/" target="_blank" rel="noreferrer noopener">Terms of Service</a> and <a href="https://www.bittorrent.com/legal/privacy-policy/" target="_blank" rel="noreferrer noopener">Privacy Policy</a>.
					</p>
				</div>
				<div class="button-container">
					<div>
	<!-- 					<router-link to="/wallet/setup">
							<Button type="text">Import instead</Button>
						</router-link> -->
					</div>
					<div>
						<Button :disabled="!passwordsMatch" :cb="handlePasswordSet">Continue</Button>
					</div>
				</div>
			</div>
			
			<div class="wallet-setup-2" v-if="current === 2">

				<div class="header-container">
					<p class="header">Wallet recovery phrase</p>
					<p class="description">
						This is your wallet recovery phrase. Please copy this phrase and store it somewhere safe as there’s no way for us to help you recover this if it’s lost. 
					</p>
				</div>
				<div class="recovery-right-container">
					<div class="recovery-answer-key-container" >
						<div v-for="(phrase, index) in recoveryPhrases"
							class="recovery-answer-key"
							:key="index">{{phrase}}
						</div>
					</div>
				</div>
				<div class="button-container copy-container">
					<Button type="copy" :copyValue="recoveryPhrases.join(' ')">Copy recovery phrase to clipboard</Button>
				</div>
				<div class="button-container">
					<div>
						<Button type="text" :cb="prevStep">Back</Button>
					</div>
					<div>
						<Button :cb="nextStep">Continue</Button>
					</div>
				</div>
			</div>

			<div class="wallet-setup-3" v-if="current === 3">
				<div class="header-container">
					<p class="header">Confirm recovery phrase</p>
					<p class="description">
						Confirm your recovery phrase by clicking on the words below and placing them in sequence in their corresponding tiles, starting from 1 and ending with 12.
					</p>
				</div>
				<div class="recovery-match-container">
					<RecoveryPhrase :choices="recoveryPhrases" @recoveryPhraseMatches="handleRecoveryPhraseMatches"/>
					<p :class="{'hidden': recoveryPhraseMatches !== false }" class="error-message">The recovery phrase sequence is incorrect.</p>
				</div>
				<div class="button-container">
					<div>
						<Button type="text" :cb="prevStep">Back</Button>
					</div>
					<div>
						<Button :disabled="recoveryPhraseMatches !== true" :cb="handleSetupComplete">Finish & create wallet</Button>
					</div>
				</div>
			</div>

	</div>
</template>

