<script>
import Button from '../components/Button.vue'
import SvgIcon from '../components/svg-icon';
import WalletBalanceTile from '../components/WalletBalanceTile';
import ExchangeTable from '../components/ExchangeTable'

export default {
  name: 'WalletDashboard',
  props: {
    id: {
      type: String,
      required: true
    },
    handleRefresh: {
      type: Function,
      default: () => {}
    },
    transactions: {
      type: Array,
      default: () => {
        return [
          {
          id: 1,
          created: '1579547824',
          amount: 200,
          type: 'Deposit',
          status: 'complete',
          }
          ]
      }
    },
    handleDeposit: {
      type: Function,
      default: () => {}
    },
    handleWithdraw: {
      type: Function,
      default: () => {}
    },
    inAppBalance: {
      type: Number,
      required: true,
      default: 0
    },
    cryptoBalance: {
      type: Number,
      required: true,
      default: 0
    }
  },
  components: {
    Button,
    'svg-icon': SvgIcon,
    WalletBalanceTile,
    'bt-exchange-table': ExchangeTable
  },
  methods: {
    handlePublicAddressCopy() {
      navigator.clipboard.writeText(this.id)
      this.showCopyTooltip = true;
      setTimeout(()=>{this.showCopyTooltip=false},1000)
    },
    paginationLeft() {
      let newIndex = this.pageIndex - 1
      if(newIndex >= 0){
        this.pageIndex = newIndex
      }
    },
    paginationRight() {
      let newIndex = this.pageIndex + 1
      if(newIndex < this.transactionsLength / this.pageSize){
        this.pageIndex = newIndex
      }
    },
    onRefresh() {

      this.handleRefresh()
    }
  },
  data() {
    return {
      showCopyTooltip: false,
      pageIndex: 0,
      pageSize: 10
    }
  },
  computed: {
    abbreviatedId() {
      return this.id.slice(0,12) + '...' + this.id.slice(-10)
    },
    transactionsLength() {
      return this.transactions.length
    },
    pageItemStart() {
      return this.pageIndex * this.pageSize
    }, 
    pageItemEnd() {
      let end = this.pageItemStart + (this.pageSize)
      return (end > this.transactionsLength) ? this.transactionsLength : end
    },
    visibleTransactions() {
      return this.transactions.slice(this.pageItemStart, this.pageItemEnd)
    }

  },
  mounted() {
    this.handleRefresh()
  }
}
</script>

<template>
  <div class="wallet-dashboard">
      <div class="wallet-dashboard-header">
        <div>
          <p class="public-address">Public Address</p>
          <p class="public-address-id">
          {{abbreviatedId}}
          <svg-icon
            :hasFill="true" 
            icon="copy" 
            class="copy-icon" 
            @click="handlePublicAddressCopy" 
            v-tooltip.bottom="{ 
              content: 'copied!', 
              classes: ['tooltip'], 
              show: showCopyTooltip,
              trigger: 'manual'
            }"
          />
        </p>
        </div>
        <div class="recovery-info" v-if="false">
          <router-link to="/wallet">
            <Button type="text">Recovery information</Button>
          </router-link>
        </div>
      </div>
      <div class="wallet-dashboard-container">
        <WalletBalanceTile 
          title="BTFS In-app Wallet Balance" 
          :balance="inAppBalance"
          :defaultTargetAddress="'cryptoWalletAddress'"
          :dropdownOptions='["BTT Crypto Wallet", "Other public address"]'
          tooltipText='This is the balance of your BTT in BTFS network, if you would like to deposit or withdraw using a crypto wallet or exchange, please use below "BTT Crypto Wallet" instead.'
          :handleWalletTransfer="handleWithdraw"
          :minTransferAmount="1000"
        >
          <span>Transferring to another public address is currently under development.</span>
        </WalletBalanceTile>
        <WalletBalanceTile 
          title="BTT Crypto Wallet Balance" 
          :balance="cryptoBalance"
          :defaultTargetAddress="'inAppWalletAddress'"
          :dropdownOptions='["BTFS In-App Wallet", "Other public address"]'
          tooltipText='BTT crypto wallet balance shows your BTT securely saved in an online wallet. You can exchange them with other tokens; transfer them to your In-app balance for use with BTFS; or send them to others'
          :handleWalletTransfer="handleDeposit"
          :minTransferAmount="10"
        >
          <span>
            Transferring to another public address is currently under development.  However, you can still do it through your Command Line interface, for more information please refer to <a class="link" href="https://docs.btfs.io/docs/withdraw-btt-from-btfs" target="_blank" rel="noreferrer noopener">this guide.</a>
          </span>
        </WalletBalanceTile>
        <div class="transaction-tile">
          <div class="title">
            transactions
          </div>
          <svg-icon
              :hasFill="true" 
              icon="refresh" 
              class="refresh-icon" 
              @click="onRefresh" 
              
          />
          <bt-exchange-table :transactions="visibleTransactions"/>
          <div class="pagination-container">
            {{transactionsLength === 0 ? 0 : pageItemStart+1}}-{{pageItemEnd}} of {{transactionsLength}}
            <svg-icon 
              :hasFill="true" 
              icon="down-arrow" 
              class="arrow-icon left"
              @click="paginationLeft" 
            />
            <svg-icon 
              :hasFill="true" 
              icon="down-arrow" 
              class="arrow-icon right"
              @click="paginationRight" 
            />
          </div>
        </div>
      </div>
    </div>
</template>

