<script>
import SlideBar from './SlideBar'
import SvgIcon  from  '../components/svg-icon.vue';

export default {
	name: 'SetStorageSize',
	components: {
		SlideBar,
		'svg-icon': SvgIcon,
	},
	props: {
		isDisabled: {
			type: Boolean,
			required: true,
			default:true
		},
		storageSpace: {
			type:Object,
			default:function() {
				return {
					minimum: {
						type: Number,
						required: false,
						default:0
					},
					used: {
						type:Number,
						required:true,
						default:0
					},
					cap: {
						type:Number,
						required:true,
						default:0
					},
					total: {
						type:Number,
						required:true,
						default:1000
					}
				}
			}
		},
		useSlider: {
			type: Boolean,
			default:true
		},
	},
	data() {
		let data = this.initData()
		return {
			current: data,
			noSlideInput:this.storageSpace.cap,
			noSlideState:''
		}
		
	},
	methods: {
		handleSlideChange(slidePercent){
			let space = getStorageUsedAndRemaining(this.storageSpace.minimum, this.storageSpace.total, slidePercent)
			this.current.used = space.used
			this.current.available = space.available
			this.$emit('slideChange', this.current.used)
		},
		handleUpdateClick() {
			let inputBelowMin = this.noSlideInput < this.storageSpace.minimum
			let inputTooLow = this.noSlideInput < this.storageSpace.used 
			let inputTooHigh = this.noSlideInput > this.storageSpace.total
			if( inputBelowMin ) {
				this.noSlideState = "commit-invalid-below"
			}
			else if ( inputTooLow ) {
				this.noSlideState = "commit-invalid-low"
			}
			else if ( inputTooHigh ) {
				this.noSlideState = "commit-invalid-high"
			}
			else if ( this.noSlideInput === this.storageSpace.cap ) {
				return
			}
			else {
				this.noSlideState = "commit-valid"
				this.current.used = this.noSlideInput
				this.current.available = this.storageSpace.total - this.noSlideInput
				this.$emit('inputChange', this.current.used)
			}
		},
		handleUndoClick() {
			this.noSlideState = ""
			this.current.used = this.storageSpace.cap
			this.current.available = this.storageSpace.total - this.storageSpace.cap
			this.noSlideInput = this.storageSpace.cap
			this.$emit('inputChange', 0)
		},
		handleInputFocus() {
			this.noSlideState = ""
			this.$emit('inputChange', 0)
		},
		handleInputBlur() {
			//round storage decimal values
			if(this.noSlideInput === ""){
				this.noSlideInput = this.storageSpace.cap
			}
			this.noSlideInput = Math.round(this.noSlideInput)
		},
		initData(){
			if(this.storageSpace.total < this.storageSpace.minimum){
				this.isDisabled = true
			}
			let initialSpaceUsedPercent = ((this.storageSpace.cap - this.storageSpace.minimum) / (this.storageSpace.total - this.storageSpace.minimum)).toFixed(3)
			initialSpaceUsedPercent = parseFloat(initialSpaceUsedPercent)
			let space = getStorageUsedAndRemaining(this.storageSpace.minimum, this.storageSpace.total, initialSpaceUsedPercent)
			if(initialSpaceUsedPercent < 0){
				initialSpaceUsedPercent = 0
			}
			else if(initialSpaceUsedPercent > 1){
				initialSpaceUsedPercent = 1
			}

			return {
				used:space.used,
				available:space.available,
				initialPercent:initialSpaceUsedPercent
			}
		}
	},
	computed:{
		earnings(){
			return gbToBTT(this.current.used)
		}
	},
	watch:{
		storageSpace : function(){
			let newData = this.initData()
			this.current = {
				...this.current,
				...newData
			}
		}
	}
};

function getStorageUsedAndRemaining(min, max, percent){
	let spaceUsed = (max - min) * percent + min
	let spaceAvailable = max - spaceUsed
	return {
		used:parseInt(spaceUsed.toFixed(0)),
		available:parseInt(spaceAvailable.toFixed(0))
	}
}

function gbToBTT(gb){
	let btt = gb * 8
	btt = parseInt(btt)
	return btt.toLocaleString()
}
</script>

<template>
	<div class="set-storage-size-container">
		<div class="storage">
			<p class="title">Set Host Storage Size</p>
			<div 
			v-if="useSlider"
			class="storage-container">
				<div class="storage-space">
					<div class="storage-space-used" :class="{disabled:this.isDisabled}">
						{{this.current.used}} GB used
					</div>
					<div class="storage-space-available" :class="{disabled:this.isDisabled}">
						{{this.current.available}} GB remaining
					</div>
				</div>
				<SlideBar
					@slideChange="handleSlideChange" 
					:isDisabled="this.isDisabled"
					:initialPercent="this.current.initialPercent"
					/>
				<div>
					<p class="error" v-if="this.isDisabled">
						<img src="../assets/svg/error.svg"/>
						32 GB is the minimum host storage commitment size
					</p>
				</div>
			</div>
			<div 
			v-if="!useSlider"
			class="no-slider-container">
				<div class="">
					<p class="subtitle">
						Local storage
					</p>
					<p>
						{{this.current.available}} GB available of {{this.storageSpace.total}} GB
					</p>
				</div>

				<div class="">
					<p class="subtitle">
						Storage commitment size
					</p>
					<input v-model="noSlideInput" @focus="handleInputFocus" @blur="handleInputBlur" type="number"> GB
					<div class="input-message-container">
						<div
						v-if="this.noSlideState === 'commit-invalid-low' || this.noSlideState === 'commit-invalid-below'"
						class="input-message invalid">
							<svg-icon icon="info" class="icon invalid" :hasFill="true" />
							<span v-if="this.noSlideState === 'commit-invalid-low'">
								You cannot commit less than the current used storage size by BTFS
							</span>
							<span v-if="this.noSlideState === 'commit-invalid-below'">
								You cannot commit less than 32 GB
							</span>
						</div>
						<div
						v-if="this.noSlideState === 'commit-invalid-high'"
						class="input-message invalid">
							<svg-icon icon="info" class="icon invalid" :hasFill="true" />
							You do not have enough space on your hard drive
						</div>
						<div
						v-if="this.noSlideState === 'commit-valid'"
						class="input-message valid">
							<svg-icon icon="check" class="icon valid" :hasFill="true" />
							Storage commitment size updated, click ‘Commit Storage’ to save
						</div>
					</div>
					<div
						class="update-button" 
						@click="handleUpdateClick"
						v-if="this.noSlideState === ''"
					>
						Update 
						<svg-icon icon="refresh" class="refresh-icon" :hasFill="true" />
					</div>
					<div
						class="update-button undo-button"
						@click="handleUndoClick"
						v-if="this.noSlideState !== ''"
					>
						<p>Undo Change</p>
						<svg-icon icon="refresh" class="refresh-icon" :hasFill="true" />
					</div>
				</div>
			</div>
		</div>
		<div class="earnings">
			<p class="title">Potential earnings per month</p>
			<p class="btt">{{this.earnings}} BTT</p>
		</div>
	</div>
</template>

